import { Component, OnInit, Input, forwardRef } from '@angular/core';
import { BASE_URL } from '../../../resources/static.resource';
import { UploadFile } from 'ng-zorro-antd';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

const DEFAULT_URL = `${BASE_URL}upload-image`;

@Component({
  selector: 'app-custom-image-upload',
  templateUrl: './custom-image-upload.component.html',
  styleUrls: ['./custom-image-upload.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CustomImageUploadComponent),
      multi: true
    }
  ]
})
/**
 * Custom upload nh
 * Link upload nh c th sa trn u component hoc nhp vo khi gi component
 * C th chnh sa phng thc truy xut response ca server ti hm onImageUpload() {}
 */
export class CustomImageUploadComponent implements OnInit, ControlValueAccessor {
  // Gi tr c component lu gi ( y l tn nh)  bind vo form khi c thay i.
  imageNames: string[];
  // Cc link nh c nhp vo t input. Do gi tr ca formControl c th l tn nh, nn cn nhp thm link khi cn thit  hin th.
  // Mc nh s set cc link nh = tn nh (trong trng hp dng chung)
  private Images: string[];
  // Cc object nh  hin th, c parse ra t cc link nh.
  // T ng setup khi set gi tr mi cho link nh.
  displayImages = [];


  get images(): string[] {
    return this.Images;
  }
  @Input()
  set images(value: string[]) {
    this.Images = value;
    this.displayImages = value ? this.Images.map((image, index) => {
      return {
        uid: 0 - index,
        name: image,
        status: 'done',
        url: image
      };
    }) : [];
  }

  // Ch  up nh. Nu chn single, gi tr form nhn c s l string, v l array khi chn multi.
  @Input() mode: 'single' | 'multi';
  // i km, v ch c tc dng vi ch  multi
  @Input() limitImagesQuantity: number;
  @Input() uploadImageUrl: string;

  private propagateChange: (_: any) => void;

  previewImage;
  previewVisible = false;

  constructor() { }

  ngOnInit() {
    if (!this.uploadImageUrl) {
      this.uploadImageUrl = DEFAULT_URL;
    }
    if (!this.mode || !['single', 'multi'].includes(this.mode)) {
      this.mode = 'single';
    }
  }

  writeValue(val: any) {
    if (val) {
      if (typeof val === 'string') {
        this.imageNames = [val];
      } else {
        this.imageNames = val;
      }
    } else {
      this.imageNames = [];
    }
    if (!this.images) {
      this.images = this.imageNames;
    }
  }

  registerOnChange(fn: any) {
    this.propagateChange = fn;
  }

  registerOnTouched() {
  }

  onUploadImage(event) {
    let willUpdate = false;
    if (event.type === 'success') {
      this.imageNames.push(event.file.response.data.name);
      willUpdate = true;
    }
    if (event.type === 'removed') {
      for (const name of this.imageNames) {
        if (name === event.file.response.data.name) {
          this.imageNames.splice(this.imageNames.indexOf(name), 1);
        }
      }
      willUpdate = true;
    }
    if (willUpdate) {
      if (this.mode === 'multi') {
        this.propagateChange(this.imageNames);
      } else {
        this.propagateChange(this.imageNames[0] || null);
      }
    }
  }

  onPreviewImage = (file: UploadFile) => {
    this.previewImage = file.url || file.thumbUrl;
    this.previewVisible = true;
  }

}
